html,
body {
  width: 100%;
  height: 100%;
}
header {
  position: fixed;
  top: 0;
  width: 90%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 9%;
  margin-bottom: 9%;
  padding: 0 5%;
  background: url(../img/header-bg.png) repeat;
}
header img {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
header p {
  -webkit-box-flex: 18;
      -ms-flex: 18;
          flex: 18;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
}
footer {
  height: 10%;
  background: #2f2f2f;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  bottom: 0;
  width: 100%;
}
/*footer dl{flex: 1;}
footer dl dt img{display: block; width: 45%; margin: 0 auto;}
footer dl dd{text-align: center;}*/
footer dl {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
footer dl dt {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
footer dl dt img {
  display: block;
  height: 45%;
}
footer dl dd {
  text-align: center;
}
